<template>
    <div class="userinfo">
        <div class="title bold">
            个人信息
        </div>
        <div class="main">
            <table>
                <tr>
                    <td class="t-title"><p class="bold">个人信息<span></span></p></td>
                    <td class="t-main">
                        <label class="cursor" for="upload" style="height: 88px;width: 88px;padding: 3px">
                            <img v-if="head" :src="head|img_mapi" height="80" width="80">
                            <img v-else src="../assets/img/head.png" height="80" width="80">
                            <input id="upload" type="file" :ref="`upload`" @change="upload" accept="image/*" style="display: none;">
                        </label>
                    </td>
                </tr>
                <tr style="margin-top: 20px;">
                    <td class="t-title"><p class="bold">会员名称<span></span></p></td>
                    <td class="t-main">
                        <!-- <input type="text" v-model="name"> -->
                        <p>{{name}}</p>
                    </td>
                </tr>
                <tr>
                    <td class="t-title"><p class="bold">性别<span></span></p></td>
                    <td class="t-main">
                        <select v-model="sex" disabled>
                            <option value="0">女</option>
                            <option value="1">男</option>
                        </select>
                    </td>
                </tr>
                <tr>
                    <td class="t-title"><p class="bold">手机号码<span></span></p></td>
                    <td class="t-main"><p>{{phone}}</p></td>
                </tr>
                <tr>
                    <td class="t-title"><p class="bold">生日<span></span></p></td>
                    <td class="t-main">
                        <!--<label v-if="!editDate" for="date" @click="SubmitDate" >{{ birth }}</label>-->
                        <!--<input v-else id="date" type="date" :value="birth" :ref="`date`" :max="new Date()" autofocus @change="getDate">-->
                        <DatePicker
                                v-model="birth"></DatePicker>
                    </td>
                </tr>
                <tr>
                    <td class="t-title"><p class="bold">机构名称<span></span></p></td>
                    <td class="t-main"><p>{{company}}</p></td>
                </tr>
                <!-- <tr>
                    <td class="t-title"><p class="bold">机构<span></span></p></td>
                    <td class="t-main"><p>{{company}}</p></td>
                </tr> -->
                <tr>
                    <td class="t-title">

                    </td>
                    <td class="t-main">
                        <div class="changePwd cursor bold ac" @click="ConfirmUserInfo">
                            确认
                        </div>
                    </td>
                </tr>
            </table>
        </div>
    </div>
</template>

<script>
    import { DatePicker } from 'element-ui'
    import 'element-ui/lib/theme-chalk/index.css';
    export default {
        name: "userinfo",
        data() {
            return {
                head: '',
                name: '王老吉',
                sex: '0',
                phone: '110',
                birth: '1970-01-01',
                company: '重庆总部',
                editDate: false
            }
        },
        metaInfo: {
            title: '我的资料',
            meta: [
                {name: 'keywords', content: '福分通'},
                {name: 'description', content: '专注企业员工福利'}
            ]
        },
        created() {
            this.getUserInfo();
        },
        methods: {
            SubmitDate(){
                this.editDate = !this.editDate
            },
            ConfirmUserInfo() {
                this.yum('api/member/updateMemberInfos', {
                    head : this.head,
                    sex : this.sex,
                    birthday : Math.floor(Date.parse(this.birth) / 1000)
                }).then(
                    res => {
                        if (res.data.status === 200) {
                            this.bus.$emit('notice', '更改成功！')
                        } else {
                            this.bus.$emit('notice', res.data.msg)
                        }
                    }
                )
            },
            getDate(e) {
                this.birth = e.target.value
                this.editDate = !this.editDate
            },
            getUserInfo() {
                this.yum('api/member/getMemberInfo', {}).then(
                    res => {
                        if (res.data.status === 200) {
                            var data = res.data.data
                            this.head = data.head
                            this.name = data.username
                            this.sex = data.sex
                            this.phone = data.mobile
                            this.company = res.data.business_name;
                            var date = new Date(data.birthday*1000)
                            var year = date.getFullYear()
                            var month = date.getMonth()+1
                            var day = date.getDate()
                            this.birth = `${year}-${month}-${day}`
                        } else {
                            this.bus.$emit('notice', res.data.msg)
                        }
                    }
                )
            },
            upload() {
                let file = this.$refs.upload.files[0];
                let URL = window.webkitURL || window.URL;
                let formData = new FormData();
                formData.append('image', file);
                this.bus.$emit('loading','上传中',false);
                this.axios.post('http://mapi.fufentong.com/www/api/file/uploads', formData).then(res => {
                    if (res.data.status === 200) {
                        this.head = res.data.data;
                        this.bus.$emit('loading','上传中',true);
                    }else{
                        this.bus.$emit('loading','上传中',true);
                        this.bus.$emit('notice','上传失败',1000);
                    }
                }).catch(
                    err => {
                        if (err) {
                            this.bus.$emit('loading','上传中',true);
                            this.bus.$emit('notice', '请检查文件是否过大', 1500);
                        }
                    }
                )
            }
        },
        components:{
            DatePicker
        }
    }
</script>

<style scoped lang="stylus">
    .userinfo
        position relative
        min-height 388px
        background-color white
        box-shadow 0 0 8px #bbb
        .title
            height 50px
            line-height 50px
            border-bottom 1px solid #ccc
            font-size 16px
            padding 0 10px
            margin 0 10px

    .main
        padding 20px

    table
        width 380px
        tr
            margin-top 10px
            display block
        .t-title
            width 80px
            height 40px
            line-height 40px
            position relative
            color black
            p
                text-align justify
                height 40px
                span
                    width 100%
                    display inline-block
        .t-main
            width 300px
            padding-left 20px
            .avatar
                width 150px
                height 150px
                display block
                border-radius 50%
            input, label
                display block
                height 40px
                line-height 40px
                width 100%
                padding 0 10px
                outline none
                border 1px solid #ccc
                border-radius 4px
            .changePwd
                width 100px
                color white
                height 40px
                line-height 40px
                font-size 16px
                background-color #ff0077
                border-radius 4px
</style>